}
.video-bg video{
  object-fit: cover;
}
.side{
  width: 343px;
  display: inline-block;
  vertical-align: top;
  background: url(/static/media/side.7148fdec.jpg) no-repeat center center;
  height: 698px;
  background-size: contain;
}
.parent {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  }
  .parent div{
    height: 210px;
    position: relative;
    overflow: hidden;
  }
  .parent h5{
    position: absolute;
    left: 10px;
    bottom: 10px;
    z-index: 2;
    font-size: 14px;
    text-shadow: 0px 0.7px 1px #000000;
    font-weight: 500;
  }
  .eye{
    position: absolute !important;
    top: 9px;
    right: 9px;
    z-index: 2 !important;
    width: 32px;
    height: 32px !important;
    background: url(data:image/png;base64,) no-repeat center center;
    background-size: contain;
  }
  .rec{
    height: 30px;
    display: flex;
    align-items: center;
    display: inline-block;
    font-size: 14px;
    font-weight: 500;
    color: #EF2111;
    background-color: #ffffff;
    border-radius: 5px;
    padding: 5px 7px;
    text-align: center;
    position: absolute;
    top: 20px;
    left: 20px;
    z-index: 2;
  }
  .rec img{
    height: 20px;
    padding-right: 4px;
  }
  
  .div1 { grid-area: 1 / 1 / 2 / 2;}
  .div2 { grid-area: 1 / 2 / 2 / 3;}
  .div3 { grid-area: 1 / 3 / 2 / 4; left: -1px;}
  .div4 { grid-area: 2 / 3 / 3 / 4; left: -1px}
  .div5 { grid-area: 2 / 2 / 3 / 3;}
  .div6 { grid-area: 2 / 1 / 3 / 2;}
  .div7 { grid-area: 3 / 1 / 4 / 2;}
  .div8 { grid-area: 3 / 2 / 4 / 3;}
  .div9 { grid-area: 3 / 3 / 4 / 4; left: -1px}

  .bg{
    background: url(/static/media/user10.5246c6b7.gif) no-repeat center center;
    background-size: cover;
  }

.navigation-footer{
  height: 77px;
  background: url(/static/media/footer.05e72b29.png) no-repeat center center;
  background-size: cover;
  position: absolute;
  z-index: 999;
  left: 0;
  right: 0;
  bottom: 0;
}
.blink-2 {
	animation: blink-2 0.9s infinite;
}
@keyframes blink-2 {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 1;
  }
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* header */

header.header-widget {
  width: 100%;
  margin: auto;
  padding: 13px 0;
