  max-width: 100%;
  overflow: visible;
}
header.header-widget .grid .gm-logo img {
  width: 115px;
}
header.header-widget .menu {
  cursor: pointer;
}
header.header-widget nav ul {
  padding: 10px;
}
header.header-widget nav ul li {
  list-style: none;
  display: inline-block;
  padding-right: 50px;
}
header.header-widget nav ul {
