/* button */

.btn-primary, a.btn-primary {
  font-size: 15px;
  line-height: 18px;
  font-weight: 500;
  color: #ffffff;
  background-color: #3330c5;
  padding: 18px 30px;
  border-radius: 1px;
  text-decoration: none;
  display: inline-block;
  transition: background-color 0.2s ease-in-out 0s;
  text-decoration: none;
}
.btn-primary:hover, a.btn-primary:hover {
  background-color: #1613ab;
  color: #ffffff !important;
}

.btn-primary-sm, a.btn-primary-sm {
  background-color: #3330c5;
  font-size: 13px;
  font-weight: 500;
  line-height: 16px;
  color: #ffffff;
  padding: 10px 20px;
  transition: background-color 0.2s ease-in-out 0s;
  text-decoration: none;
}
.btn-primary-sm:hover, a.btn-primary-sm:hover {
  background-color: #1613ab;
  color: #ffffff;
  text-decoration: none;
}

.btn-secondary {
  font-size: 15px;
  line-height: 18px;
  font-weight: 500;
  color: #3330c5;
  background-color: #ffffff;
  border: 1px solid #3330c5;
  padding: 18px 30px;
  border-radius: 1px;
  text-decoration: none;
  display: inline-block;
  transition: background-color 0.2s ease-in-out 0s;
  text-decoration: none;
}
.btn-secondary:hover {
  border: 1px solid #1613ab;
}

.btn-secondary-sm {
  background-color: #ffffff;
  font-size: 13px;
  font-weight: 600;
  line-height: 16px;
  color: #3330c5;
  border: 1px solid #3330c5;
  padding: 10px 20px;
  transition: background-color 0.2s ease-in-out 0s;
  text-decoration: none;
}
.btn-secondary-sm:hover {
  border: 1px solid #1613ab;
}

a.btn-link {
  font-size: 20px;
  color: #3330c5;
  text-decoration: none;
  font-weight: 600;
  position: relative;
  display: inline-block;
  padding-right: 26px;
}

