/* mobile responsive */
@media screen and (min-width: 320px) and (max-width: 767px) {
  .hide-on-mobile {
    display: none !important;
    width: 0;
    height: 0;
  }
  .main-container section {
    padding: 50px 20px;
  }
  .main-container p {
    font-size: 14px;
    line-height: 22px;
  }
  .main-container section .section-heading{
    width: 100%;
  }
  a.btn-link {
    font-size: 16px;
  }
  header.header-widget {
    padding: 0;
  }
  header.header-widget .gm-logo {
    padding: 10px;
  }
  header.header-widget .header-content {
    width: 100%;
  }
  header.header-widget .header-content [class*="col-"] {
    display: inline-block;
    vertical-align: top;
    margin-bottom: 0;
  }
  header.header-widget .header-content .col-4-12 {
    float: right;
  }
  header.header-widget .grid .gm-logo img {
    width: 74px;
  }
  .sidebar .inside-scroll .intro {
    padding-bottom: 10px;
  }
  input {
    min-width: 100%;
  }
  header.header-widget .header-content .grid .row{
    display: inline;
    display: initial;
  }
}


/* for tablet */
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .hide-on-mobile {
    display: none !important;
    width: 0;
    height: 0;
  }
  header.header-widget .header-content .col-4-12{
    float: right;
  }
  header.header-widget .header-content .grid .row{
    display: inline;
    display: initial;
  }
}

/*
  Simple Grid
  Project Page - http://thisisdallas.github.com/Simple-Grid/
  Author - Dallas Bass
  Site - http://dallasbass.com
*/

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  font-size: 100%;
  color: #FAFAFA;
  background-color: #202124;
  text-align: center;
}

/* ROOT FONT STYLES */

* {
  font-family: "Inter", sans-serif;
  font-weight: normal;
  font-style: normal;
  line-height: 1.5;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
  color: #FAFAFA;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

/* TYPOGRAPHY */

h1 {
  font-size: 62px;
  font-family: "Inter", sans-serif;
  font-weight: 800;
  line-height: 86px;
  font-style: normal;
  font-display: swap;
  color: #111111;
  margin: 0;
}

h2 {
  font-size: 40px;
  font-family: "Inter", sans-serif;
  font-weight: 800;
  line-height: 47px;
  margin-bottom: 30px;
}

h3 {
  font-size: 25px;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  line-height: 34px;
  color: #111111;
}

h4 {
  font-size: 1.125rem;
}

h5 {
  font-size: 1rem;
}

h6 {
  font-size: 0.875rem;
}

.font-light {
  font-weight: 300;
}

.font-regular {
  font-weight: 400;
}

.font-heavy {
  font-weight: 700;
}

/* POSITIONING */

.left {
  text-align: left;
}
.right {
  text-align: right;
}
.center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}
.justify {
  text-align: justify;
}
.overflow {
  overflow: hidden;
}
.visible {
  overflow: visible !important;
}
.light-blue {
  background-color: #f5f9ff;
}
.dark-blue {
  background-color: #040339;
}
.relative {
  position: relative;
}
strong {
  font-weight: 600;
}
.no-cursor {
  cursor: default;
}
.highlight {
  font-weight: 500;
  color: #111111;
}

[class*="grid"],
[class*="col-"],
[class*="mobile-"],
.grid:after {
  box-sizing: border-box;
}

[class*="col-"] {
  float: left;
  min-height: 1px;
  padding-right: 20px; /* column-space */
}

[class*="col-"] [class*="col-"]:last-child {
  padding-right: 0;
}

.grid {
  width: 100%;
  max-width: 90%;
  min-width: 748px; /* when using padded grid on ipad in portrait mode, width should be viewport-width - padding = (768 - 20) = 748. actually, it should be even smaller to allow for padding of grid containing element */
  margin: 0 auto;
  overflow: hidden;
}

.grid:after {
  content: "";
  display: table;
  clear: both;
}

.grid-pad {
  padding-top: 20px;
  padding-left: 20px; /* grid-space to left */
  padding-right: 0; /* grid-space to right: (grid-space-left - column-space) e.g. 20px-20px=0 */
}

.push-right {
  float: right;
}

